<template>
    <div>
        <v-header>
        </v-header>

        <v-content :type="'none'">
            <p class="tip">请选择支付方式</p>
            <div class="card">
                <span class="img bank"></span>
                <p class="card-info">{{card.name}} <span class="sort-num">尾号{{card.card}}</span></p>
                <span class="img bingo"></span>
            </div>
            <div class="section" style="margin-top:.2rem;">
                <div class="matter">
                    <div class="title">金额</div>
                    <div class="edit" style="width:75%;">
                        <input class="text" placeholder="请输入充值金额" v-model="money"
                               type="tel" style="width:100%;" maxlength="8"/>
                    </div>
                </div>
            </div>
            <v-inner-btn>
                <input :class="['input recharge',money.length > 0 && parseInt(money) > 0 && !isNaN(money) ? '':'opa-6']"
                       style="margin-top:.3rem;"
                       value="充值" type="button" @click="toRecharge"/>
            </v-inner-btn>
        </v-content>
        <div class="paypwd-box z-index-max tran-scale" v-show="openPaybox">
            <div class="warp">
                <span class="img close" @click="closeRecharge"></span>
                <div class="title">请输入支付密码</div>
                <div class="pay-detail">充值<span style="color:#ffa800;">{{money | currency ''}}</span>元</div>
                <div class="pwd-box">
                    <input type="tel" class="opa-hide real-box" v-model="payPwd" maxlength="6" id="payPwd"/>
                    <label for="payPwd">
                        <ul class="dis-flex process">
                            <li v-for="n in 6" :class="['flex-1',payPwd.length > n ? 'has':'']"></li>
                        </ul>
                    </label>
                </div>
                <a class="forget-paypwd" v-link="{name:'trade-TradePsd',query:{from:'/wallet/recharge'}}">忘记支付密码</a>
            </div>
        </div>
    </div>
</template>
<script>
    import VHeader from '../../components/Header'
    import {DJUtils} from '../../assets/js/common'
    import VContent from '../../components/Content'
    import VInnerBtn from '../../components/InnerBtn'
    import {toast} from '../../assets/js/toast'
    import {lt, url} from '../../const'
    import $ from 'jquery'

    export default{
        route: {
            data({from, to}){
                this.from = to.query.from;
                this.payPwd = '';
                this.money = '';
                this.openPaybox = false;
                return this.$http.get(url.getBankCard).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            if (data.isBank) {
                                if (!data.isPayPsd) {
                                    if (from.name == 'set-tradePsd') {
                                        DJUtils.backHistory();
                                        return;
                                    }
                                    toast.alert('请先设置支付密码');
                                    window.location.href = "#/wallet/setTradePsd?type=1";
                                    return;
                                }
                                this.card = data.bank[0];
                            } else {
                                if (data.realName) {
                                    if (from.name == 'add-card') {
                                        DJUtils.backHistory();
                                        return;
                                    }
                                    toast.alert('请先绑定银行卡');
                                    window.location.href = "#/wallet/addCard?type=1&realName=" + data.realName;
                                } else {
                                    if (from.name == 'real-step1') {
                                        DJUtils.backHistory();
                                        return;
                                    }
                                    toast.alert('请先实名认证');
                                    window.location.href = "#/identify/real/step1?type=1";
                                }
                            }
                        } else {
                            toast.alert(resultMsg);
                        }
                    }
                );
            }
        },
        data(){
            return {
                card: {},
                money: '',
                payPwd: '',
                openPaybox: false,
                from: ''
            }
        },
        methods: {
            toRecharge(){
                let money = this.money;
                if (money.length > 0 && parseInt(money) > 0 && !isNaN(money)) {
                    this.openPaybox = true;
                    toast.mask(null, true);
                    this.$nextTick(function () {
                        $("#payPwd")[0].focus();
                    });
                }
            },
            closeRecharge(){
                this.openPaybox = false;
                toast.loadEnd();
            },
            doRecharge(){
                this.$http.get(url.recharge, {
                    money: this.money,
                    payPsd: this.payPwd,
                }).then(function ({data:{success, resultMsg}}) {
                        $("#payPwd").attr('disabled', false);
                        this.payPwd = '';
                        if (success) {
                            this.closeRecharge();
                            toast.alert('充值成功');
                            if (this.from) {
                                DJUtils.backHistory();
                            } else {
                                window.location.href = "#/wallet/home";
                            }
                        } else {
                            toast.mask(null, true);
                            toast.alert(resultMsg);
                        }
                    }
                );
            }
        },
        watch: {
            'payPwd': function () {
                if (this.payPwd.length == 6) {
                    $("#payPwd").attr('disabled', true);
                    this.doRecharge();
                }
            }
        },
        components: {VHeader, VContent, VInnerBtn}
    }
</script>
<style scoped>
    .tip {
        height: .35rem;
        line-height: .35rem;
        font-size: .12rem;
        color: #999;
        text-indent: .1rem;
    }

    .card {
        height: .55rem;
        line-height: .55rem;
        text-align: left;
        border-bottom: .01rem solid #e5e5e5;
        border-top: .01rem solid #e5e5e5;
        position: relative;
        background-color: #fff;
    }

    .img.bank {
        position: absolute;
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        background-image: url(../../assets/img/wallet/bank.png);
        left: .15rem;
        top: .15rem;
    }

    .card .card-info {
        padding-left: .55rem;
    }

    .card .sort-num {
        padding-left: .1rem;
        font-size: .12rem;
        color: #999;
    }

    .img.bingo {
        position: absolute;
        top: .2rem;
        right: .1rem;
        width: .19rem;
        height: .13rem;
        background-image: url(../../assets/img/icon/select01.png);
        background-size: .19rem;
    }
</style>



